
@charset "UTF-8";
.bookMain {
  /*  */
}
.bookMain_iconList {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    padding-bottom: 15px;
    background-color: #fff;
    margin-bottom: 10px;
}
.bookMain_iconsingle {
    margin-top: 15px;
    width: 33.33%;
}
.bookMain_iconsingle > img {
      display: block;
      width: 40px;
      height: 40px;
      margin: 0 auto;
      margin-bottom: 15px;
}
.bookMain_iconsingle > h3 {
      font-size: 14px;
      color: #333;
      text-align: center;
}
.bookMain_content > h3 {
    padding: 0 15px;
    box-sizing: border-box;
    font-size: 18px;
    line-height: 40px;
    height: 40px;
    color: #2196f3;
    background-color: #fff;
}



