
.goodsList_List {
  height: calc(100% - 47px);
  box-sizing: border-box;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  padding-top: 40px;
  padding-bottom: 40px;
}
.goodsList_main {
  height: 100%;
  background-color: #f1f1f1;
}
.goodsList_main .vux-header {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.goodsList_main .vux-header .vux-header-left, .goodsList_main .vux-header .vux-header-right {
    position: relative;
    top: 5px;
}
.goodsList_main .vux-header .vux-header-left .left-arrow {
    top: 0;
}
.goodsList_main .vux-header-title {
    width: 0;
    margin: 0;
}
.goodsList_main .vux-header-right {
    -webkit-box-flex: 1;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    margin-left: 40px;
}
.goodsList_main .weui-loadmore_line .weui-loadmore__tips {
    background-color: #f3f3f3 !important;
}
.goodsList_search {
  border-radius: 5px;
  background-color: #f5f5f5;
  box-sizing: border-box;
  font-size: 14px;
  color: #6d6d6d;
  height: 30px;
  line-height: 30px;
}
.goodsList_search > i {
    float: left;
    font-size: 20px;
    color: #bfbfbf;
    margin-left: 10px;
}
.goodsList_chenckList {
  z-index: 999;
  position: fixed;
  top: 46px;
  left: 0;
  width: 100%;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  box-sizing: border-box;
  padding: 0 35px;
  -webkit-box-pack: start;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  height: 41px;
  line-height: 40px;
  background-color: #f1f1f1;
}
.goodsList_chenckList > span {
    margin-right: 60px;
    width: 100px;
    white-space: nowrap;
}
.goodsList_chenckList--selected {
    color: #2196f3;
}
.goodsList_noGoods {
  position: fixed;
  width: 100%;
  top: 88px;
  height: 60%;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.goodsList_noGoods > i {
    font-size: 150px;
    color: #999;
}
.goodsList_noGoods > p {
    margin-top: 20px;
}



