
.select_main {
  height: calc(100% - 47px);
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: #f1f1f1;
}
.select_main_imgBox {
    position: relative;
}
.select_main_textBox {
    position: absolute;
    bottom: 15px;
    left: 0;
    width: 100%;
    box-sizing: border-box;
    padding: 0 15px;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-align: end;
    -webkit-align-items: flex-end;
            align-items: flex-end;
}
.select_main_textBox > img {
      height: 45px;
      width: 45px;
      border-radius: 3px;
}
.select_main_textBox > h3 {
      font-size: 17px;
      color: #fff;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
}
.select_main_textBox > h3:nth-of-type(1) {
        -webkit-box-flex: 1;
        -webkit-flex-grow: 1;
                flex-grow: 1;
        text-indent: 7.5px;
}
.select_main_textBox > h3:nth-of-type(2) {
        -webkit-flex-shrink: 0;
                flex-shrink: 0;
        padding-right: 7.5px;
}
.select_main_textBox > i {
      font-size: 17px;
      color: #fff;
}
.select_main_goodsList {
    white-space: nowrap;
    padding: 35px 15px;
    overflow-x: auto;
    width: 100%;
    box-sizing: border-box;
}
.select_main_goodsSingle {
    display: inline-block;
    width: 110px;
}
.select_main_goodsSingle:not(:last-of-type) {
      margin-right: 25px;
}
.select_main_goodsTitle {
    font-size: 13px;
    color: #666;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    margin: 15px 0;
}
.select_main_goodsText {
    font-size: 12px;
    text-align: center;
    color: #e31111;
}
.select_main_goodsText > span {
      font-size: 11px;
      color: #666;
      text-decoration: line-through;
}
.select_main_goodsImg {
    display: black;
    height: 110px;
    width: 110px;
}
.select_main_img {
    width: 100%;
    display: block;
}



