.bookMain_age_starList {
    height: calc(100% - 300px);
    position: relative;
    -webkit-perspective: 800;
            perspective: 800;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
}
.bookMain_age_starList > li {
      -webkit-animation: star 3s linear infinite;
              animation: star 3s linear infinite;
      width: 60%;
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      -webkit-box-pack: center;
      -webkit-justify-content: center;
              justify-content: center;
      -webkit-box-align: center;
      -webkit-align-items: center;
              align-items: center;
}
.bookMain_age_starList > li > i {
        font-size: 180px;
        color: #2196f3;
