@-webkit-keyframes star {
0% {
    opacity: 0.3;
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
}
50% {
    opacity: 1;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}
100% {
    opacity: 0.3;
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
}
}
@keyframes star {
0% {
    opacity: 0.3;
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
}
50% {
    opacity: 1;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}
100% {
    opacity: 0.3;
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
}
}
