
.weui-dialog__btn_primary {
  color: #2196f3 !important;
}
.addressList_main {
  background-color: #f3f3f3;
  height: 100%;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}
.addressList_main .vux-check-icon > .weui-icon-success:before,
  .addressList_main .vux-check-icon > .weui-icon-success-circle:before {
    color: #2196f3;
}
.addressList_main .weui-dialog__btn_primary {
    color: #2196f3;
}
.addressList_main .weui-loadmore_line .weui-loadmore__tips {
    background-color: #f3f3f3 !important;
}
.addressList_confrim {
  height: 70%;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 15px;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
  color: #272727;
}
.addressList_confrim > i {
    font-size: 150px;
    color: #c1c1c1;
}
.addressList_addBtn {
  height: 50px;
  font-size: 18px;
  background: #2196f3;
  line-height: 50px;
  text-align: center;
  width: 100%;
  display: block;
  position: fixed;
  bottom: 0;
  left: 0;
  color: #fff;
  z-index: 99999;
}
.addressList_list {
  padding-bottom: 70px;
}
.addressList_single {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  max-height: 150px;
  min-height: 120px;
  background-color: #fff;
  box-sizing: border-box;
  padding: 0 15px;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin-bottom: 10px;
  font-size: 15px;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  color: #272727;
}
.addressList_single_name {
    display: block;
    width: 50%;
    line-height: 50px;
    height: 50px;
}
.addressList_single_phone {
    display: block;
    width: 50%;
    line-height: 50px;
    text-align: right;
    height: 50px;
}
.addressList_single_address {
    display: block;
    width: 100%;
    max-height: 60px;
    min-height: 30px;
    line-height: 30px;
}
.addressList_single_action {
    height: 40px;
    width: calc(100% + 30px);
    margin: 0 -15px;
    box-sizing: border-box;
    padding: 0 15px;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    border-top: 1px solid #eee;
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
}
.addressList_single_action > span:nth-of-type(1) {
      -webkit-box-flex: 1;
      -webkit-flex-grow: 1;
              flex-grow: 1;
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
}
.addressList_single_action > span:nth-of-type(2) {
      -webkit-box-flex: 0;
      -webkit-flex-grow: 0;
              flex-grow: 0;
      width: 60px;
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      -webkit-box-align: center;
      -webkit-align-items: center;
              align-items: center;
      -webkit-box-pack: justify;
      -webkit-justify-content: space-between;
              justify-content: space-between;
      box-sizing: border-box;
      margin-right: 10px;
}
.addressList_single_action > span:nth-of-type(3) {
      -webkit-box-flex: 0;
      -webkit-flex-grow: 0;
              flex-grow: 0;
      width: 55px;
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      -webkit-box-pack: justify;
      -webkit-justify-content: space-between;
              justify-content: space-between;
      -webkit-box-align: center;
      -webkit-align-items: center;
              align-items: center;
      box-sizing: border-box;
}
.addressList_single_icon {
    height: 18px;
    width: 18px;
    border: 2px solid #dddddd;
    border-radius: 3px;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin-right: 5px;
}
.addressList_single_icon > i {
      display: none;
}
.addressList_single_icon--default {
      border-color: #2196f3;
      background-color: #2196f3;
}
.addressList_single_icon--default > i {
        color: #fff;
        display: block;
}



