
.search_main {
  background-color: #f3f3f3;
  height: 100%;
}
.search_header {
  box-sizing: border-box;
  padding: 10px 20px;
  height: 50px;
  background-color: #fff;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 999;
  border-bottom: 1px solid #f3f3f3;
}
.search_search {
  width: 85%;
  border-radius: 5px;
  background-color: #f5f5f5;
  box-sizing: border-box;
  font-size: 14px;
  color: #6d6d6d;
  height: 30px;
  line-height: 30px;
  -webkit-box-flex: 1;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
}
.search_search > i {
    float: left;
    font-size: 20px;
    color: #bfbfbf;
    margin-left: 10px;
}
.search_search > input {
    -webkit-box-flex: 1;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    background-color: #f5f5f5;
    border: none;
    border-radius: 5px;
}
.search_menu {
  width: 45px;
  -webkit-box-flex: 0;
  -webkit-flex-grow: 0;
          flex-grow: 0;
  display: block;
  line-height: 30px;
  text-align: right;
  font-size: 15px;
  color: #313130;
}
.search_history {
  position: fixed;
  top: 50px;
  width: 100%;
  left: 0;
}
.search_history_title {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
    box-sizing: border-box;
    padding: 0 20px;
    height: 60px;
}
.search_history_title > h2 {
      font-weight: 400;
      color: #333;
      font-size: 15px;
}
.search_history_title > button {
      border: none;
      background: transparent;
      color: #333;
      font-size: 15px;
}
.search_history_list {
    box-sizing: border-box;
    padding: 0 20px;
}
.search_history_list > li {
      height: 30px;
      line-height: 30px;
      display: inline-block;
      margin-right: 10px;
      background-color: #fff;
      padding: 0 10px;
      font-size: 16px;
      margin-bottom: 10px;
      border-radius: 3px;
      text-align: center;
}



