
.help_common_text {
  padding: 0 15px;
}
.help_common_text > h3 {
    padding-top: 20px;
    font-size: 14px;
    color: #272727;
    font-weight: 400;
}
.help_common_text > p {
    font-size: 14px;
    color: #272727;
    margin-top: 20px;
}
.card_main {
  height: calc(100% - 47px);
  overflow-y: auto;
  background-color: #f1f1f1;
  box-sizing: border-box;
  padding-top: 40px;
  position: relative;
}
.card_main_typeList {
    height: 40px;
    width: 100%;
    background-color: #fff;
    position: absolute;
    top: 0;
    left: 0;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    box-sizing: border-box;
    padding: 0 30px;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.card_main_typeList > span {
      width: 80px;
      height: 38px;
      line-height: 38px;
      font-size: 16px;
      color: #272727;
      text-align: center;
      display: block;
      border-bottom: 2px solid #fff;
}
.card_main_typeList--select {
      color: #2196f3 !important;
      border-color: #2196f3 !important;
}
.card_main_list {
    height: 100%;
    overflow-y: auto;
    padding-top: 20px;
    box-sizing: border-box;
    padding-bottom: 20px;
}
.card_main_single {
    color: #fff;
    position: relative;
    width: 90%;
    margin: 0 auto;
    margin-bottom: 10px;
}
.card_main_single--select {
      color: #c1c1c1;
}
.card_main_single--select button {
        border-color: #c1c1c1;
        color: #c1c1c1;
}
.card_main_single--select .card_main_single_title {
        color: #6d6d6d;
}
.card_main_single--select .card_main_single_price {
        color: #6d6d6d;
}
.card_main_single--select .card_main_single_store {
        color: #6d6d6d;
}
.card_main_single > img {
      display: block;
      min-height: 100px;
      width: 100%;
}
.card_main_single_box {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      box-sizing: border-box;
      padding: 20px 0;
}
.card_main_single_left {
      width: 35%;
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -webkit-flex-direction: column;
              flex-direction: column;
      -webkit-box-pack: justify;
      -webkit-justify-content: space-between;
              justify-content: space-between;
      -webkit-box-align: center;
      -webkit-align-items: center;
              align-items: center;
}
.card_main_single_right {
      position: relative;
      width: 65%;
      text-indent: 20px;
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -webkit-flex-direction: column;
              flex-direction: column;
      -webkit-box-pack: justify;
      -webkit-justify-content: space-between;
              justify-content: space-between;
}
.card_main_single_price {
      font-size: 11px;
}
.card_main_single_price > span {
        font-size: 24px;
}
.card_main_single_store {
      font-size: 10px;
}
.card_main_single_rules {
      font-size: 11px;
}
.card_main_single_title {
      font-size: 16px;
}
.card_main_single_time {
      font-size: 11px;
}
.card_main_single_btn {
      position: absolute;
      display: block;
      width: 50px;
      height: 20px;
      font-size: 12px;
      color: #fff;
      text-align: center;
      border: 1px solid #fff;
      border-radius: 5px;
      line-height: 20px;
      top: 0;
      right: 20px;
}



