
#register_main {
  width: 100%;
}
#register_main .left-arrow {
    display: none;
}
#register_main .vux-header-right {
    right: 20px;
}
#register_main .vux-header-left {
    left: 20px;
}
#register_main .vux-header-right, #register_main .vux-header-left {
    height: 45px;
    line-height: 45px;
    top: 0;
}
#register_main .vux-no-group-title {
    margin-top: 0;
}
#register_main .vux-header-title {
    color: #333;
    font-size: 18px;
    height: 45px;
    line-height: 45px;
}
#register_main .vux-header {
    padding: 0;
}
#register_main .iconfont {
    margin-right: 20px;
    font-size: 25px;
}
#register_main .weui-cell:after {
    display: none;
}
#register_main .weui-cell:before {
    display: none;
}
#register_main .weui-cells:after {
    display: none;
}
#register_main .weui-cells:before {
    display: none;
}
#register_main .vux-header-back, #register_main .vux-header-right {
    padding: 0;
    color: #333;
    font-size: 16px;
}
#register_main .vux-header-back > a, #register_main .vux-header-right > a {
      color: #333;
}
#register_main .vux-x-input {
    clear: both;
    margin-top: 7.5px;
    height: 45px;
    line-height: 45px;
    color: #c1c1c1;
    border: 1px solid #dddddd;
    box-sizing: border-box;
    font-size: 14px;
    border-radius: 5px;
}
#register_main .register_link {
    float: left;
    margin-left: 15px;
    color: #349ced;
    margin-top: 7.5px;
    font-size: 14px;
}
#register_main .register_lforget {
    float: right;
    margin-right: 15px;
    margin-top: 7.5px;
    color: #999;
    font-size: 14px;
}
#register_main .register_header {
    background-color: #fff;
    height: 45px;
    color: #333;
    border-bottom: 1px solid #dddddd;
}
#register_main .register_img {
    display: block;
    height: 160px;
    margin: 30px  auto;
}
#register_main .register_group {
    box-sizing: border-box;
    padding: 15px;
}
#register_main .register_getcode {
    width: 60%;
    float: left;
}
#register_main .register_btn {
    height: 45px;
    background-color: #2196f3;
    margin-top: 15px;
    font-size: 15px;
    color: #fff;
    border-radius: 5px;
}
#register_main .register_code {
    width: 37%;
    float: right;
    margin-top: 7.5px !important;
    background-color: #f3f3f3;
    color: #333;
}
#register_main .register_code--disable {
      background-color: transparent;
}
#register_main .register_book {
    font-size: 13px;
    color: #999;
    margin-top: 10px;
}
#register_main .register_book > a {
      color: #2196f3;
}



