
#login_code {
  width: 100%;
}
#login_code .left-arrow {
    display: none;
}
#login_code .vux-header-right {
    right: 20px;
}
#login_code .vux-header-left {
    left: 20px;
}
#login_code .vux-header-right, #login_code .vux-header-left {
    height: 45px;
    line-height: 45px;
    top: 0;
}
#login_code .vux-no-group-title {
    margin-top: 0;
}
#login_code .vux-header-title {
    color: #333;
    font-size: 18px;
    height: 45px;
    line-height: 45px;
}
#login_code .vux-header {
    padding: 0;
}
#login_code .iconfont {
    margin-right: 20px;
    font-size: 25px;
}
#login_code .weui-cell:after {
    display: none;
}
#login_code .weui-cell:before {
    display: none;
}
#login_code .weui-cells:after {
    display: none;
}
#login_code .weui-cells:before {
    display: none;
}
#login_code .vux-header-back, #login_code .vux-header-right {
    padding: 0;
    color: #333;
    font-size: 16px;
}
#login_code .vux-header-back > a, #login_code .vux-header-right > a {
      color: #333;
}
#login_code .vux-x-input {
    clear: both;
    margin-top: 7.5px;
    height: 45px;
    line-height: 45px;
    color: #c1c1c1;
    border: 1px solid #dddddd;
    box-sizing: border-box;
    font-size: 14px;
    border-radius: 5px;
}
#login_code .logincode_link {
    float: left;
    margin-left: 15px;
    color: #349ced;
    margin-top: 7.5px;
    font-size: 14px;
}
#login_code .logincode_lforget {
    float: right;
    margin-right: 15px;
    margin-top: 7.5px;
    color: #999;
    font-size: 14px;
}
#login_code .logincode_header {
    background-color: #fff;
    height: 45px;
    color: #333;
    border-bottom: 1px solid #dddddd;
}
#login_code .logincode_img {
    display: block;
    height: 80px;
    margin: 15px  auto;
    margin-bottom: 0;
}
#login_code .logincode_group {
    box-sizing: border-box;
    padding: 15px;
}
#login_code .logincode_getcode {
    width: 60%;
    float: left;
}
#login_code .logincode_btn {
    height: 45px;
    background-color: #2196f3;
    margin-top: 65px;
    font-size: 15px;
    color: #fff;
    border-radius: 5px;
}
#login_code .logincode_code {
    width: 37%;
    float: right;
    margin-top: 7.5px !important;
    background-color: #f3f3f3;
    color: #333;
}
#login_code .logincode_code--disable {
      background-color: transparent;
}
#login_code .logincode_book {
    font-size: 13px;
    color: #999;
    margin-top: 10px;
}
#login_code .logincode_book > a {
      color: #2196f3;
}



