
.seeting_main .weui-cells__title {
  background-color: #f3f3f3;
  margin: 0;
  height: 50px;
  line-height: 50px;
}
.seeting_main .weui-cell {
  height: 45px;
  line-height: 45px;
}
.seeting_main .weui-cells {
  margin: 0;
}
.seeting_main .weui-cell__ft {
  font-size: 15px;
}
.seeting_main_btn {
  width: calc(100% - 30px);
  margin: 0 auto;
  display: block;
  background-color: #2196f3;
  color: #fff;
  height: 50px;
  line-height: 50px;
  text-align: center;
  border-radius: 5px;
  margin-top: 20px;
}



