
.help_common_text {
  padding: 0 15px;
}
.help_common_text > h3 {
    padding-top: 20px;
    font-size: 14px;
    color: #272727;
    font-weight: 400;
}
.help_common_text > p {
    font-size: 14px;
    color: #272727;
    margin-top: 20px;
}
.return_main {
  height: 100%;
  background-color: #f1f1f1;
}
.return_main .weui-cell__ft {
    font-size: 15px;
    font-weight: 400;
}
.return_main .weui-cells {
    margin: 0;
}
.return_main .weui-cell {
    height: 34px;
}
.return_main .weui-cells:before {
    display: none;
}
.return_main_input {
    text-indent: 15px;
    height: 50px;
    line-height: 50px;
    border: none;
    font-size: 15px;
    color: #272727;
    width: 100%;
}
.return_main_tplSingle {
    height: 40px;
    line-height: 40px;
    font-size: 15px;
    text-align: center;
    color: #272727;
}
.return_main_sinceList {
    max-height: 350px;
    overflow-y: auto;
    box-sizing: border-box;
}
.return_main_addressInfo {
    background-color: #fff;
    max-height: 110px;
    padding: 15px;
    box-sizing: border-box;
    border-bottom: 1px solid #eee;
}
.return_main_addressInfo .main_since_text {
      height: 30px;
      font-size: 15px;
      line-height: 30px;
      color: #272727;
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      -webkit-box-pack: justify;
      -webkit-justify-content: space-between;
              justify-content: space-between;
      font-weight: 400;
      -webkit-box-flex: 0;
      -webkit-flex-grow: 0;
              flex-grow: 0;
}
.return_main_addressInfo .main_since_title {
      font-weight: 400;
}
.return_main_addressInfo .main_since_price {
      font-weight: 400;
}
.return_main_addressInfo .main_since_intr {
      -webkit-box-flex: 1;
      -webkit-flex-grow: 1;
              flex-grow: 1;
      font-size: #666;
      font-size: 15px;
      line-height: 25px;
}
.return_main_prompt {
    height: 50px;
    line-height: 50px;
    width: 90%;
    margin: 70px auto;
    display: block;
    background-color: #2196f3;
    font-size: 15px;
    text-align: center;
    color: #fff;
    border-radius: 5px;
}
.return_main_tpl {
    position: absolute;
    bottom: 0;
    height: 40px;
    left: 0;
    line-height: 40px;
    width: 100%;
    font-size: 15px;
    text-align: center;
    color: #fff;
    background-color: #2196f3;
}
.return_main_title {
    box-sizing: border-box;
    padding: 0 15px;
    height: 50px;
    line-height: 50px;
    font-size: 15px;
    color: #272727;
    background-color: #f3f3f3;
}
.return_main_tplbox {
  height: 350px;
  overflow-y: auto;
  box-sizing: border-box;
  padding-bottom: 70px;
}



