
.book_common .vux-header .vux-header-left > i {
  color: #333 !important;
}
.book_common > .vux-header {
  height: 50px;
  box-sizing: border-box;
  background-color: #fff !important;
  box-shadow: 0 1px 1px 0px rgba(0, 0, 0, 0.2);
  border-bottom: 1px solid #eee;
}
.book_common > .vux-header .vux-header-title {
    color: #333;
    font-size: 18px;
}
.book_common .vux-header .vux-header-left a,
.book_common .vux-header .vux-header-left button,
.book_common .vux-header .vux-header-right a,
.book_common .vux-header .vux-header-right button {
  color: #333;
  font-size: 13px;
}
.book_common .vux-header .vux-header-left .left-arrow:before {
  border-color: #333;
}
body .vux-actionsheet-menu-default {
  color: #2196f3;
}



