
.list_compent_list {
  background-color: #f1f1f1;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  min-height: calc(100% + 10px);
}
.list_compent_single {
  width: 49.5%;
  box-sizing: border-box;
  padding: 10px;
  background-color: #fff;
  margin-top: 5px;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
}
.list_compent_img {
  -webkit-box-flex: 1;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
  box-sizing: border-box;
}
.list_compent_img > img {
    margin: 0 auto;
    width: 100%;
    display: block;
}
.list_compent_title {
  margin-top: 5px;
  color: #272727;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-weight: 400;
}
.list_compent_price {
  font-weight: 400;
  color: #f80000;
  font-size: 15px;
}
.list_compent_oldprice {
  color: #272727;
  font-size: 13px;
  text-decoration: line-through;
}
.list_compent_text {
  box-sizing: border-box;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
}



