
@charset "UTF-8";
body .dp-header .dp-item.dp-right {
  color: #2196f3;
}
.bookMain {
  /*  */
}
.bookMain_age_content {
    height: calc(100% - 50px);
    overflow-y: auto;
}
.bookMain_age_content .weui-cells {
      margin: 0;
}
.bookMain_age_content .weui-cell {
      padding: 12px 15px;
      color: #333;
      font-size: 16px;
}
.bookMain_age_content .weui-icon-success {
      color: #2196f3;
}
.bookMain_age_content .weui-btn_primary {
      background-color: #2196f3;
}
.bookMain_age_content .weui-btn_primary:not(.weui-btn_disabled):active {
      background-color: #2196f3;
}
.bookMain_age_content .weui-msg__desc {
      margin: 30px auto;
      width: 70%;
}
.bookMain_fail_content {
  height: calc(100% - 50px);
}
.bookMain_fail_content img {
    width: 100%;
    display: block;
}



