
.feature_content {
  height: calc(100% - 50px);
  overflow-y: auto;
}
.y-header {
  height: 50px;
  width: 100%;
  background: #fff;
}
.y-header .left-arrow::before {
    border-color: #1e1e1e !important;
}
.y-header .vux-header-title span {
    font-size: 20px;
    color: #1e1e1e;
}
.y-header span {
    color: #2196f3;
    font-size: 15px;
}
.y-content:not(:first-of-type) {
  margin-top: 10px;
}
.feature_img {
  width: 100%;
  display: block;
}



