
#forget_main {
  width: 100%;
}
#forget_main .left-arrow {
    display: none;
}
#forget_main .vux-header-right {
    right: 20px;
}
#forget_main .vux-header-left {
    left: 20px;
}
#forget_main .vux-header-right, #forget_main .vux-header-left {
    height: 45px;
    line-height: 45px;
    top: 0;
}
#forget_main .vux-header-title {
    color: #333;
    font-size: 18px;
    height: 45px;
    line-height: 45px;
}
#forget_main .vux-header {
    padding: 0;
}
#forget_main .iconfont {
    margin-right: 20px;
    font-size: 25px;
}
#forget_main .weui-cells:after {
    display: none;
}
#forget_main .weui-cells:before {
    display: none;
}
#forget_main .vux-header-back, #forget_main .vux-header-right {
    padding: 0;
    color: #333;
    font-size: 16px;
}
#forget_main .vux-header-back > a, #forget_main .vux-header-right > a {
      color: #333;
}
#forget_main .vux-x-input {
    clear: both;
    margin-top: 7.5px;
    height: 45px;
    line-height: 45px;
    color: #c1c1c1;
    border: 1px solid #dddddd;
    box-sizing: border-box;
    font-size: 14px;
    border-radius: 5px;
}
#forget_main .forget_link {
    float: left;
    margin-left: 15px;
    color: #349ced;
    margin-top: 7.5px;
    font-size: 14px;
}
#forget_main .forget_lforget {
    float: right;
    margin-right: 15px;
    margin-top: 7.5px;
    color: #999;
    font-size: 14px;
}
#forget_main .forget_header {
    background-color: #fff;
    height: 45px;
    color: #333;
    border-bottom: 1px solid #dddddd;
}
#forget_main .forget_img {
    display: block;
    height: 80px;
    margin: 15px auto;
    margin-bottom: 0;
}
#forget_main .forget_group {
    box-sizing: border-box;
    padding: 15px;
}
#forget_main .forget_getcode {
    width: 60%;
    float: left;
}
#forget_main .forget_btn {
    height: 45px;
    background-color: #2196f3;
    margin-top: 15px;
    font-size: 15px;
    color: #fff;
    border-radius: 5px;
}
#forget_main .forget_code {
    width: 37%;
    float: right;
    margin-top: 7.5px !important;
    background-color: #f3f3f3;
    color: #333;
}
#forget_main .forget_code--disable {
      background-color: transparent;
}
#forget_main .forget_book {
    font-size: 13px;
    color: #999;
    margin-top: 10px;
}
#forget_main .forget_book > a {
      color: #2196f3;
}



