
.help_main .weui-cells__title {
  background-color: #f3f3f3;
  margin: 0;
  height: 50px;
  line-height: 50px;
}
.help_main .weui-cell {
  height: 45px;
  line-height: 45px;
}
.help_main .weui-cell__ft {
  font-size: 15px;
}
.authent_main {
  height: 100%;
  background-color: #f3f3f3;
}
.authent_main .weui-label {
    font-size: 15px;
}
.authent_main .vux-x-input {
    height: 30px;
}
.authent_main .weui-input {
    font-size: 15px;
    font-weight: 400;
}
.authent_main .weui-cells {
    margin: 0;
}
.authent_main .weui-cells:after {
      display: none;
}
.authent_main .weui-cells:before {
      display: none;
}
.authent_main_confrim {
    margin: 20px 0;
    font-size: 15px;
    color: #999;
    text-indent: 40px;
}
.authent_main_btn {
    width: 90%;
    background-color: #2196f3;
    color: #fff;
    font-size: 15px;
    line-height: 40px;
    height: 40px;
    text-align: center;
    margin: 0 auto;
    border-radius: 5px;
    display: block;
}



