
.shop_main {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
  background-color: #f1f1f1;
}
.shop_main .weui-loadmore_line .weui-loadmore__tips {
    background-color: #f1f1f1 !important;
}
.shop_main_goodsList {
    -webkit-flex-shrink: 1;
            flex-shrink: 1;
    -webkit-box-flex: 1;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
}
.shop_main_header {
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    -webkit-box-flex: 0;
    -webkit-flex-grow: 0;
            flex-grow: 0;
    height: 115px;
    position: relative;
    overflow: hidden;
}
.shop_main_shopface {
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.shop_main_cell {
    height: 40px;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    box-sizing: border-box;
    padding: 0 15px;
    background-color: rgba(0, 0, 0, 0.5);
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
    padding-left: 100px;
}
.shop_main_cell > i {
      color: #fff;
      font-size: 20px;
      width: 10px;
}
.shop_main_logo {
    height: 65px;
    width: 65px;
    display: block;
    position: absolute;
    left: 20px;
    bottom: 7px;
}
.shop_main_title {
    font-size: 15px;
    color: #fff;
    -webkit-box-flex: 1;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.shop_main_cardListbox {
    padding: 10px 0;
    width: 100%;
    overflow-x: auto;
    background-color: #f1f1f1;
    white-space: nowrap;
    overflow-y: auto;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    padding-right: 0;
    height: 87px;
    box-sizing: border-box;
}
.shop_main_cardList {
    /* height: 100%; */
    display: block;
    height: 100%;
    box-sizing: border-box;
    /* padding: 0 20px; */
    overflow: auto;
    white-space: nowrap;
    width: 100%;
}
.shop_main_box {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    box-sizing: border-box;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    color: #fff;
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
    height: 100%;
    width: 105px;
    padding: 10px 0;
    font-size: 13px;
}
.shop_main_cardSingle {
    display: inline-block;
    background-image: url("../../assets/img/common/storeCard.png");
    background-size: cover;
    height: 67.5px;
    width: 135px;
    margin-right: 15px;
}
.shop_main_cardSingle:first-of-type {
      margin-left: 15px;
}
.shop_main_price > span {
    font-size: 20px;
}



