
.help_common_title {
  padding-top: 47px;
}
.help_common_title .vux-header {
    background-color: #fff;
    border-bottom: 1px solid #dadada;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 9999;
}
.help_common_title .vux-header .vux-header-title {
      color: #272727;
}
.help_common_title .vux-header .vux-header-left, .help_common_title .vux-header .vux-header-right {
    color: #272727;
}
.help_common_title .vux-header .vux-header-left .left-arrow:before {
    border-color: #272727;
}
.opinion_main {
  overflow: hidden;
  height: 100%;
  background-color: #f3f3f3;
  box-sizing: border-box;
  padding: 0 15px;
}
.opinion_main .weui-cells:before {
    display: none;
}
.opinion_main .weui-cells:after {
    display: none;
}
.opinion_main_textarea {
    border: 1px solid #dadada;
}
.opinion_main_title {
    font-weight: 400;
    font-size: 14px;
    color: #272727;
    margin-top: 30px;
    margin-bottom: 10px;
}
.opinion_main_input {
    border-radius: 5px;
    height: 40px;
    width: 100%;
    line-height: 40px;
    border: none;
    border: 1px solid #dadada;
    box-sizing: border-box;
    padding: 5px;
}
.opinion_main_btn {
    height: 40px;
    width: 100%;
    text-align: center;
    line-height: 40px;
    font-size: 15px;
    color: #fff;
    background-color: #2196f3;
    border-radius: 5px;
    display: block;
    margin: 0 auto;
    margin-top: 20px;
}



