
body {
  background-color: #f3f3f3;
}
body .weui-cell {
    padding: 8px 15px;
}
body .vux-label {
    font-size: 15px;
    color: #272727;
}
body .mine_container .iconfont {
    font-size: 20px;
    margin-right: 10px;
}
.mine_container {
  height: 100%;
  background-color: #f1f1f1;
}
.mine_container .weui-cells {
    margin-top: 0;
}
.mine_header {
  height: 200px;
  background-image: url("../../assets//img/index/minebc.png");
  overflow: hidden;
}
.mine_avatar {
  display: block;
  height: 75px;
  width: 75px;
  border-radius: 50%;
  padding: 5px;
  border: 2px solid #fff;
  margin: 0 auto;
  margin-top: 45px;
}
.mine_name {
  display: block;
  margin: 10px auto;
  overflow: hidden;
  font-size: 18px;
  color: #fff;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 200px;
  text-align: center;
}



