
.collention_main {
  background-color: #fff;
  height: 100%;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}
.collention_main--haveGoods {
    background-color: #fff;
}
.collention_main .weui-loadmore_line .weui-loadmore__tips {
    background-color: #fff !important;
}
.collention_nocoll {
  height: 60%;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 40px;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.collention_nocoll > i {
    font-size: 150px;
    color: #c1c1c1;
}
.collention_nocoll > p {
    margin-top: 20px;
    color: #272727;
}
.collention_nocoll_link {
    font-size: 18px;
    height: 50px;
    width: 150px;
    line-height: 50px;
    color: #fff;
    text-align: center;
    border-radius: 5px;
    background-color: #2196f3;
    margin-top: 40px;
}



