
.bookMain_carContent .weui-cell {
  padding: 12px 15px;
}
.bookMain_carContent .vux-label {
  font-size: 16px;
}
.bookMain_carContent .wapper {
  height: calc(100% - 87px);
}
body .weui-cells {
  margin-top: 0;
}
.mui-checkbox {
  -webkit-appearance: none;
  position: relative;
  width: 25px;
  height: 25px;
  margin-right: 10px;
  background-color: #FFFFFF;
  border: solid 1px #999;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  background-clip: padding-box;
  display: inline-block;
}
.mui-checkbox:focus {
    outline: 0 none;
    outline-offset: -2px;
}
.mui-checkbox--select:before {
    display: block;
    width: 60%;
    position: absolute;
    border-radius: 50%;
    height: 60%;
    background-color: #2196f3;
    left: 20%;
    top: 20%;
    content: "";
    color: #FFFFFF;
    font-size: 18px;
}
.mui-checkbox:checked {
    /* background-color: #18b4ed; */
}
.mui-checkbox:checked:before {
      display: block;
      width: 60%;
      position: absolute;
      border-radius: 50%;
      height: 60%;
      background-color: #2196f3;
      left: 20%;
      top: 20%;
      content: "";
      color: #FFFFFF;
      font-size: 18px;
}
.mui-checkbox:disabled {
    background-color: #d9d9d9;
    border: solid 1px #d9d9d9;
}
.mui-checkbox:disabled:before {
      display: block;
      width: 60%;
      position: absolute;
      border-radius: 50%;
      height: 60%;
      background-color: #2196f3;
      left: 20%;
      top: 20%;
      content: "";
      color: #FFFFFF;
      font-size: 18px;
}
.mui-checkbox:disabled:before {
      background-color: #eee;
}
.mui-checkbox.checkbox-green:checked {
    background-color: #5cb85c;
}
.mui-checkbox.checkbox-orange:checked {
    background-color: #f0ad4e;
}
.mui-checkbox.checkbox-s {
    width: 19px;
    height: 19px;
}
.mui-checkbox.checkbox-s:before {
      display: block;
      width: 60%;
      position: absolute;
      border-radius: 50%;
      height: 60%;
      background-color: #2196f3;
      left: 20%;
      top: 20%;
      content: "";
      color: #FFFFFF;
      font-size: 13px;
}
.mui-checkbox-anim {
  -webkit-transition: background-color ease 0.2s;
  transition: background-color ease 0.2s;
}
body .vux-prev-icon,
body .vux-next-icon {
  color: #2196f3;
  border-color: #2196f3;
}
body .vux-popup-header-title {
  color: #333;
  font-size: 16px;
}
body .vux-popup-header-right {
  color: #2196f3;
}
body .inline-calendar td.is-today,
body .inline-calendar td.is-today.is-disabled {
  color: #2196f3;
}
body .inline-calendar td.current > span.vux-calendar-each-date {
  background-color: #2196f3;
}



