
.help_common_title .vux-header {
  background-color: #fff;
  border-bottom: 1px solid #dadada;
}
.help_common_title .vux-header .vux-header-title {
    color: #272727;
}
.help_common_title .vux-header .vux-header-left, .help_common_title .vux-header .vux-header-right {
  color: #272727;
}
.help_common_title .vux-header .vux-header-left .left-arrow:before {
  border-color: #272727;
}
.editAddress_main {
  overflow: hidden;
  height: 100%;
  background-color: #f3f3f3;
  box-sizing: border-box;
}
.editAddress_main .weui-cells {
    margin: 0;
}
.editAddress_main .weui-cells:before {
      display: none;
}
.editAddress_main .weui-cells:after {
      display: none;
}
.editAddress_main .vux-x-textarea {
    font-size: 14px;
}
.editAddress_main .vux-x-input {
    font-size: 15px;
    color: #272727;
    height: 30px;
}
.editAddress_main .vux-cell-box {
    font-size: 15px;
    color: #272727;
    height: 46px;
    line-height: 33px;
}
.editAddress_main_textarea {
    border: 1px solid #dadada;
}
.editAddress_main_title {
    font-weight: 400;
    font-size: 14px;
    color: #272727;
    margin-top: 30px;
    margin-bottom: 10px;
}
.editAddress_main_input {
    border-radius: 5px;
    height: 40px;
    width: 100%;
    line-height: 40px;
    border: none;
    border: 1px solid #dadada;
    box-sizing: border-box;
    padding: 5px;
}
.editAddress_main_btn {
    height: 40px;
    width: 90%;
    text-align: center;
    line-height: 40px;
    font-size: 15px;
    color: #fff;
    background-color: #2196f3;
    border-radius: 5px;
    display: block;
    margin: 0 auto;
    margin-top: 40px;
}
.editAddress_main_default {
    height: 45px;
    line-height: 45px;
    background-color: #fff;
    margin-top: 15px;
    box-sizing: border-box;
    padding: 0 15px;
}
.editAddress_main_default .vux-check-icon {
      float: right;
}
.editAddress_main_default .vux-check-icon > .weui-icon-success:before, .editAddress_main_default .vux-check-icon > .weui-icon-success-circle:before {
      color: #2196f3;
}



