
.about_main {
  height: 100%;
  background-color: #fff;
  box-sizing: border-box;
  padding: 25px 30px 0 30px;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
}
.about_main_logo {
    width: 50px;
}
.about_main_title {
    font-size: 23px;
    color: #2197f3;
    margin: 10px 0;
}
.about_main_code {
    font-size: 12px;
    color: #999;
}
.about_main_text {
    height: 25px;
    margin-top: 20px;
    margin-bottom: 40px;
}
.about_main_intr {
    text-indent: 30px;
    line-height: 20px;
    color: #666;
    font-size: 13px;
    margin-bottom: 30px;
}
.about_main_footer {
    font-size: 12px;
    color: #666;
    margin-top: 50px;
}



