
body {
  background-color: #f3f3f3;
}
body .weui-tabbar {
    background-color: #fff;
}
.list_listbox {
  position: fixed;
  width: 100%;
  height: calc(100% - 50px);
  width: calc(100% - 100px);
  left: 100px;
  top: 50px;
  overflow-y: auto;
  background-color: #f3f3f3;
}
.list_typelist {
  position: fixed;
  top: 50px;
  width: 100px;
  background-color: #fff;
  height: calc(100% - 50px);
  left: 0;
}
.list_typelist_single {
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: 15px;
    color: #272727;
}
.list_typelist_single--selected {
      color: #fff;
      background-color: #2196f3;
}
.list_header {
  box-sizing: border-box;
  padding: 10px 20px;
  height: 50px;
  background-color: #fff;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 999;
  border-bottom: 1px solid #f3f3f3;
}
.list_search {
  width: 85%;
  border-radius: 5px;
  background-color: #f5f5f5;
  box-sizing: border-box;
  font-size: 14px;
  color: #6d6d6d;
  height: 30px;
  line-height: 30px;
  -webkit-box-flex: 1;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}
.list_search > i {
    float: left;
    font-size: 20px;
    color: #bfbfbf;
    margin-left: 10px;
}
.list_menu {
  width: 45px;
  text-align: right;
  -webkit-box-flex: 0;
  -webkit-flex-grow: 0;
          flex-grow: 0;
  display: block;
  line-height: 30px;
  font-size: 14px;
  color: #313130;
}
.list_recommend_list {
  background-color: #f3f3f3;
  box-sizing: border-box;
  padding: 10px;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.list_recommend_single {
  background-color: #fff;
  width: 32%;
  margin-right: 1%;
  box-sizing: border-box;
  padding: 20px 10px;
  padding-bottom: 10px;
  margin-bottom: 5px;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.list_recommend_img {
  display: block;
  box-sizing: border-box;
}
.list_recommend_img > img {
    margin: 0 auto;
    width: 100%;
    display: block;
}
.list_recommend_title {
  color: #272727;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-weight: 400;
}
.list_recommend_price {
  color: #f80000;
  font-size: 15px;
}
.list_recommend_text {
  box-sizing: border-box;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
}



