@-webkit-keyframes hongbao {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
25% {
    -webkit-transform: rotate(-30deg);
            transform: rotate(-30deg);
}
50% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
75% {
    -webkit-transform: rotate(30deg);
            transform: rotate(30deg);
}
100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
}
@keyframes hongbao {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
25% {
    -webkit-transform: rotate(-30deg);
            transform: rotate(-30deg);
}
50% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
75% {
    -webkit-transform: rotate(30deg);
            transform: rotate(30deg);
}
100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
}
