
.order_logmain {
  background-color: #f3f3f3;
  height: 100%;
}
.order_logmain .vux-header-right > span {
    font-size: 18px;
    color: #272727;
}
.order_logmain .weui-loadmore_line .weui-loadmore__tips {
    background-color: #f3f3f3;
}
.order_type_list {
  padding: 10px 0;
  background-color: #fff;
}
.order_type_title {
  height: 50px;
  line-height: 50px;
  text-indent: 15px;
  font-size: 15px;
  color: #272727;
}
.order_type_single {
  font-size: 15px;
  color: #272727;
  position: relative;
  padding-left: 25px;
  height: 20px;
  margin-left: 15px;
}
.order_type_single:after {
    top: 0;
    height: 18px;
    width: 18px;
    border-radius: 50%;
    left: 0;
    display: block;
    content: "";
    position: absolute;
    border: 1px solid #dadada;
}
.order_type_single:before {
    height: 10px;
    width: 10px;
    background-color: #2196f3;
    position: absolute;
    left: 5px;
    top: 5px;
    display: none;
    content: "";
    border-radius: 50%;
    position: absolute;
}
.order_type_single--selected:after {
    border-color: #2196f3;
}
.order_type_single--selected:before {
    display: block;
}



