
.shop_nofunction {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  height: 80%;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
  position: fixed;
  top: 0;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  left: 0;
  width: 100%;
}
.shop_nofunction > p {
    text-align: center;
    line-height: 35px;
    color: #272727;
    font-size: 18px;
}
.shop_nofunction > i {
    font-size: 160px;
    color: #999;
}
.shop_nofunction_download {
    display: block;
    width: 150px;
    height: 35px;
    line-height: 35px;
    text-align: center;
    font-size: 18px;
    color: #fff;
    border-radius: 5px;
    margin-top: 25px;
    background-color: #2196f3;
}



