
.wapper {
  -webkit-overflow-scrolling: touch;
  height: 100%;
  overflow-y: auto;
  box-sizing: border-box;
  position: relative;
}
.wapper_content {
    min-height: calc(100% + 1px);
}
.wapper .swiper_pulldown {
    color: #2176f2;
    top: -60px;
    position: absolute;
    left: calc(50% - 15px);
    width: 30px;
    margin: 0 auto;
    font-size: 25px;
    text-align: center;
    height: 60px;
    overflow: hidden;
    line-height: 60px;
}
.wapper .swiper_data {
    text-align: center;
    padding-top: 20px;
    padding-bottom: 30px;
    color: #666;
}
.wapper .swiper_pullload {
    width: 60px;
    margin: 0 auto;
    font-size: 25px;
    text-align: center;
    height: 60px;
    overflow: hidden;
    line-height: 60px;
    color: #2176f2;
    -webkit-animation: loading 1s  linear infinite;
            animation: loading 1s  linear infinite;
}
.wapper .swiper_pullload--text {
      display: block;
      width: 100%;
      font-style: normal;
      margin: 0 auto;
      font-size: 25px;
      color: #666;
      text-align: center;
      height: 60px;
      text-align: center;
      overflow: hidden;
      font-size: 14px;
      line-height: 60px;
}
.wapper .swiper_height {
    height: 0;
    -webkit-transition: all 0.1s ease-in;
    transition: all 0.1s ease-in;
}
@-webkit-keyframes loading {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes loading {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
.wapper .swiper_rote {
    -webkit-transition: all 0.1s ease-in;
    transition: all 0.1s ease-in;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}



