
.help_common_text {
  padding: 0 15px;
}
.help_common_text > h3 {
    padding-top: 20px;
    font-size: 14px;
    color: #272727;
    font-weight: 400;
}
.help_common_text > p {
    font-size: 14px;
    color: #272727;
    margin-top: 20px;
}
.settlement_main {
  background-color: #f1f1f1;
  height: calc(100% - 47px);
  overflow-y: auto;
}
.settlement_main_mess {
    box-sizing: border-box;
    padding: 15px;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    background-color: #fff;
}
.settlement_main_shopTitle {
    height: 50px;
    text-indent: 15px;
    line-height: 50px;
    font-size: 15px;
    color: #272727;
    margin-top: 10px;
    border-bottom: 1px solid #eee;
    background-color: #fff;
}
.settlement_main_img {
    display: block;
    height: 100px;
    width: 100px;
}
.settlement_main_text {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    box-sizing: border-box;
    padding: 10px;
    padding-right: 0;
    -webkit-align-content: space-between;
            align-content: space-between;
}
.settlement_main_text > span {
      font-size: 15px;
      color: #272727;
}
.settlement_main_text > span:nth-of-type(1) {
        width: 100%;
        display: block;
}
.settlement_main_text > span:nth-of-type(2) {
        width: 100%;
        display: block;
}
.settlement_main_text > span:nth-of-type(3) {
        color: #f80000;
        width: 50%;
        display: block;
}
.settlement_main_text > span:nth-of-type(4) {
        width: 50%;
        display: block;
        text-align: right;
}
.settlement_main_priceColl .weui-cells {
    margin: 0;
    margin-bottom: 10px;
}
.settlement_main_priceColl .weui-cell {
    height: 34px;
}
.settlement_main_priceColl .weui-cell .weui-cell__ft {
      color: #f80000;
      font-size: 16px;
}
.settlement_main_priceColl .weui-cell__ft {
    font-size: 15px;
    color: #666;
}
.settlement_main_priceColl .weui-textarea {
    font-size: 15px;
    color: #272727;
}



