
.shopInfo_main {
  height: 100%;
  background-color: #fff;
}
.shopInfo_main .weui-cells__title {
    background-color: #f3f3f3;
    margin: 0;
    height: 50px;
    line-height: 50px;
}
.shopInfo_main .weui-cell {
    height: 45px;
    line-height: 45px;
}
.shopInfo_main .weui-cell__ft {
    font-size: 15px;
}
.shopInfo_main_header {
    height: 115px;
    position: relative;
    margin-bottom: 80px;
}
.shopInfo_main_shopface {
    height: 115px;
    width: 100%;
}
.shopInfo_main_cell {
    position: absolute;
    top: 80px;
    height: 100px;
    left: 0;
    width: 100%;
    box-sizing: border-box;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.shopInfo_main_logo {
    height: 65px;
    width: 65px;
    display: block;
}
.shopInfo_main_title {
    font-size: 15px;
    color: #272727;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}



