
.orderInfon_main {
  height: 100%;
  box-sizing: border-box;
  overflow-y: auto;
  background-color: #f3f3f3;
}
.orderInfon_main .weui-cells {
    margin: 0;
}
.orderInfon_main .weui-cell:before {
    left: 0;
}
.orderInfon_main .main_since_single {
    margin: 0;
    border-top: 1px solid #eee;
}
.orderInfon_main_card {
    font-size: 15px;
    color: #272727;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
}
.orderInfon_main_card button {
      border: 1px solid #2196f3;
      color: #2196f3;
      padding: 2px 10px;
      border-radius: 5px;
      margin-right: 10px;
}
.orderInfon_main_card span:last-of-type {
      color: #f80000;
      -webkit-box-flex: 1;
      -webkit-flex-grow: 1;
              flex-grow: 1;
      text-align: right;
      box-sizing: border-box;
      padding-right: 20px;
}
.orderInfon_main_cardclose {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 50px;
    line-height: 50px;
    background-color: #2196f3;
    font-size: 15px;
    color: #fff;
    text-align: center;
    display: block;
    width: 100%;
}
.orderInfon_main_address {
    background-color: #fff;
}
.orderInfon_main_address .weui-cell:nth-of-type(1) {
      height: 54px;
}
.orderInfon_main_address .weui-cell:nth-of-type(2) {
      height: 34px;
}
.orderInfon_main_address .nodefault {
      height: 34px !important;
}
.orderInfon_main_timeRange {
    box-sizing: border-box;
    padding: 0 15px;
    height: 40px;
    line-height: 40px;
    color: #272727;
    font-size: 15px;
    background-color: #fff;
    margin: 10px 0;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.orderInfon_main_mess {
    box-sizing: border-box;
    padding: 15px;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    background-color: #fff;
}
.orderInfon_main_shopTitle {
    height: 50px;
    text-indent: 15px;
    line-height: 50px;
    font-size: 15px;
    color: #272727;
    margin-top: 10px;
    border-bottom: 1px solid #eee;
    background-color: #fff;
}
.orderInfon_main_img {
    display: block;
    height: 100px;
    width: 100px;
}
.orderInfon_main_text {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    box-sizing: border-box;
    padding: 10px;
    padding-right: 0;
    -webkit-align-content: space-between;
            align-content: space-between;
}
.orderInfon_main_text > span {
      font-size: 15px;
      color: #272727;
}
.orderInfon_main_text > span:nth-of-type(1) {
        width: 100%;
        display: block;
}
.orderInfon_main_text > span:nth-of-type(2) {
        color: #f80000;
        width: 50%;
        display: block;
}
.orderInfon_main_text > span:nth-of-type(2) > span {
          color: #272727;
          font-size: 13px;
          text-decoration: line-through;
}
.orderInfon_main_text > span:nth-of-type(3) {
        width: 50%;
        display: block;
        text-align: right;
}
.orderInfon_main_priceColl .weui-cell:not(:last-of-type) {
    height: 34px;
}
.orderInfon_main_priceColl .weui-cell__ft {
    font-size: 15px;
    color: #f80000;
}
.orderInfon_main_priceColl .weui-textarea {
    font-size: 15px;
    color: #272727;
}
.orderInfon_footer {
  z-index: 999;
  height: 60px;
  line-height: 60px;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: #fff;
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.1);
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
}
.orderInfon_footer_price {
    box-sizing: border-box;
    padding-left: 15px;
    -webkit-box-flex: 1;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    font-size: 14px;
    color: #272727;
}
.orderInfon_footer_price > div {
      width: 100%;
      height: 50%;
      line-height: 38px;
}
.orderInfon_footer_price span {
      font-size: 18px;
      color: #f80000;
}
.orderInfon_footer_price > p {
      line-height: 25px;
      display: block;
      width: 100%;
      color: #999;
}
.orderInfon_footer_btn {
    font-size: 18px;
    color: #fff;
    line-height: 60px;
    width: 120px;
    text-align: center;
    height: 60px;
    background-color: #2196f3;
}
.orderInfo_main_content {
  height: calc(100% - 107px);
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}



